import React, { useEffect, useState, useRef, useMemo } from 'react';
import Grid from '@mui/material/Grid2';
import { FormControl, InputLabel, Select, MenuItem, 
    TextField, Button, Paper, Typography, 
    Stack,
    Chip, Box,
    stepButtonClasses, useFormControl, IconButton, 
    InputBase,
    InputAdornment,
    Checkbox,
    Switch, FormControlLabel, FormLabel, Snackbar, Alert,
    List, ListItem, ListItemText,
    FormGroup} from '@mui/material';
import { FixedSizeList } from 'react-window';
import SearchIcon from '@mui/icons-material/Search';
import LayersClearIcon from '@mui/icons-material/LayersClear';
import shadows from '@mui/material/styles/shadows';
import useSerialPort from './useSerialPort';

function LogWindow() {
    const { logs, setLogs } = useSerialPort();
    const [searchTerm, setSearchTerm] = useState('');
    const [enableListEndRef, setEnableListEndRef] = useState(true);
    const listEndRef = useRef(null);
    const [alertOpen, setAlertOpen] = useState(false);  // Snackbar

    // logs       
    useEffect(() => {
        if(enableListEndRef && listEndRef.current) {
            listEndRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [logs, enableListEndRef]);

    const handleClearLogs = () => {
        setLogs([]);
        setAlertOpen(true);
    }

    //          
    const handleEanbleListEndRef = (event) => {
        setEnableListEndRef(event.target.checked);
    };

    //    
    const filteredLogs = useMemo(() => {
        return logs.filter((log) => 
            log.message.toLowerCase().includes(searchTerm.toLowerCase()) ||
            log.timestamp.includes(searchTerm));
    }, [logs, searchTerm]);

    // snackbar 
    const handleAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setAlertOpen(false);
    };

    // react-window Row 
    // const Row = {{ index, style }}

    return (
        <Box sx={{
            p: '10px',
            alignItems: 'center',
            width: '400',
            margin: '10px',
            boxShadow: 'rgba(99, 99, 99, 0.2) 0px 2px 8px 0px',
            maxHeight: '100vh',
        }}>
            {/* Snackbar  */}
            <Snackbar
                open={alertOpen}
                autoHideDuration={1000}
                onClose={handleAlertClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleAlertClose} severity="success" sx={{ width: '100%' }}>
                     !
                </Alert>
            </Snackbar>

            <Box sx={{ 
                marginTop: '10px', 
                alignItems:'center',
                }}>
                <TextField
                    id='input-search-textfield'
                    label=" "
                    size='small'
                    slotProps={{
                        input: {
                            startAdornment: (
                                <InputAdornment position="start">
                                    <SearchIcon />
                                </InputAdornment>
                            )
                        }
                    }}
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                />
                <Switch
                    checked={enableListEndRef}
                    onChange={handleEanbleListEndRef}
                    inputProps={{ 'aria-label': 'controlled' }}
                    label="  !"
                    />
                {/*     */}
                <IconButton onClick={handleClearLogs}><LayersClearIcon /></IconButton>
            </Box>

            <Box
                sx={{
                    maxHeight: '60vh',
                    overflow: 'auto',
                    margin: '10px',
                    fontFamily: 'consolas',
                }}>
                <List disablePadding>
                    {filteredLogs.map((log, index) => (
                        <ListItem  key={index} disablePadding>
                            <ListItemText 
                                primary={`[${log.timestamp}] ${log.message}`} />
                        </ListItem>
                    ))}
                    <div ref={listEndRef} />
                </List>
            </Box>

            
        </Box>
    );
}

export default LogWindow;