import React from 'react';
import { DragDropContext, Droppable, Draggable } from '@hello-pangea/dnd';    //    
import { v4 as uuidv4 } from 'uuid';  //  ID  
import Grid from '@mui/material/Grid2';
import { FormControl, InputLabel, Select, MenuItem, 
    TextField, Button, Paper, Typography, 
    Stack,
    Chip, Box,
    stepButtonClasses, useFormControl, IconButton } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import DragHandleIcon from '@mui/icons-material/DragHandle';
import AddCircle from '@mui/icons-material/AddCircle';
import useSerialPort from './useSerialPort';

function DataInput() {
    const { dataItems, setDataItems } = useSerialPort();    // useSerialPort  

    const isValidHex = (hex) => /^[0-9A-Fa-f\s]*$/.test(hex);   // Hex    

    // Hex   
    const formatHexInput = (value) => {
        //  ,  
        const cleanValue = value.replace(/\s+/g, '').toUpperCase();
        // 2   
        const formattedValue = cleanValue.match(/.{1,2}/g).join(' ') ?? '';
        return formattedValue;
    };

    const handleDataChange = (id, field, value) => {
        if(field === 'data') {  // data
            if(!isValidHex(value)) { //   Hex  
                return;
            }
            
            // Hex   (only data field)
            value = value ? formatHexInput(value) : ''; //   ,   
        } 

        //  
        setDataItems((prevDataItems) => {
            return prevDataItems.map((item) => //   
                item.id === id          // id 
                ? { ...item, [field]: value } // 
                : item  //    
            );
        });
    };

    const addDataItem = () => {
        const newItem = { id: uuidv4(), data: '', interval: 1000, description: '' };
        setDataItems([...dataItems, newItem]);
    };

    const removeDataItem = (id) => {
        // const newDataItems = dataItems.filter((_, i) => i !== index);
        const newDataItems = dataItems.filter((item) => item.id !== id); // id 
        setDataItems(newDataItems);
    };

    //    
    const onDragEnd = (result) => {
        if(!result.destination) {
            return;
        }

        const newDataItems = Array.from(dataItems);                         //  
        const [movedItem] = newDataItems.splice(result.source.index, 1);    //  
        newDataItems.splice(result.destination.index, 0, movedItem);        //  

        setDataItems(newDataItems);
    };

    return (
        <Box sx={{ mt:1 }}>
            <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="dataItems">
                {(provided) => (
                <Box {...provided.droppableProps} ref={provided.innerRef}>
                    {dataItems.map((item, index) => (
                    <Draggable key={item.id} draggableId={item.id} index={index}>
                        {(provided) => (
                        <Box 
                            component="section"
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            sx={{
                                display: 'flex',
                                alignItems: 'center',
                                ...provided.draggableProps.style,
                                justifyContent:'center',
                                margin: '10px',
                            }}
                        >
                            <Stack direction="row" spacing={2} sx={{justifyContent:'center'}}>
                                <Box 
                                    key={`section-${index}`}
                                    {...provided.dragHandleProps}
                                    style={{ cursor: 'grab', display: 'flex', alignItems: 'center' }}
                                > 
                                    <DragHandleIcon></DragHandleIcon>
                                </Box>
                                <TextField
                                    id={`data-${index}`}
                                    name={`data-${index}`}
                                    label={`HEX Data ${index + 1}`}
                                    multiline
                                    maxRows={1}
                                    sx={{minWidth: '204px'}}
                                    value={item.data}
                                    size='small'
                                    onChange={(e) => handleDataChange(item.id, 'data', e.target.value)}
                                />

                                <TextField
                                    id={`interval-${index}`}
                                    name={`interval-${index}`}
                                    label={`interval ${index + 1}`}
                                    multiline
                                    maxRows={1}
                                    sx={{minWidth: '204px'}}
                                    size='small'
                                    slotProps={{
                                        input: {
                                            type: 'number',
                                            inputProps: { min: 0 }
                                        }
                                    }}
                                    value={item.interval}
                                    
                                    onChange={(e) => {
                                        const v = Number(e.target.value);
                                        const numValue = Number(v);
                                        handleDataChange(item.id, 'interval', v === '' ? '' : isNaN(numValue) ? 10 : numValue <= 10 ? 10 : numValue);   //   0 10 ..
                                    }}
                                        
                                />

                                <TextField
                                    id={`description-${index}`}
                                    name={`description-${index}`}
                                    label={` ${index + 1}`}
                                    multiline
                                    maxRows={1}
                                    sx={{minWidth: '204px'}}
                                    size='small'
                                    value={item.description}
                                    onChange={(e) => handleDataChange(item.id, 'description', e.target.value)}
                                />
                                <IconButton aria-label='delete' onClick={() => removeDataItem(item.id)}><DeleteIcon/></IconButton>
                            </Stack>
                        </Box>
                        )}
                    </Draggable>
                    ))}
                    {provided.placeholder}
                </Box>
                )}
            </Droppable>
            </DragDropContext>
            
            <IconButton onClick={addDataItem}><AddCircle/></IconButton>
        </Box>
    );
}


export default DataInput;
