import React from "react";
import Modal from "@mui/material/Modal";
import { TextField, Box, Button, Typography, Snackbar, ButtonGroup } from "@mui/material";
import Alert from "@mui/material/Alert";
import { modalStyle } from "./styles/modalStyles";

function ExportModal({ dataItems, onClose }) {
    const dataStr = JSON.stringify(dataItems, null, 2); //  2
    const [open, setOpen] = React.useState(true);
    const [alertOpen, setAlertOpen] = React.useState(false);

    const handleClose = () => { setOpen(false); onClose(); };
    const handleCopy = () => {
        navigator.clipboard.writeText(dataStr)
            .then(() => {
                setAlertOpen(true);
            })
            .catch(err => {
                console.error('  :.', err);
            });
    };
    const handleAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setAlertOpen(false);
    };

    return (
        <Modal 
            open={open}
            onClose={onClose}
            aria-labelledby="modal-modal-title"
        >
            <Box sx={modalStyle}>
                <Typography variant="h6" id="modal-modal-title" component="h2"> </Typography>
                <TextField
                    id='input-export-textfield'
                    label=" "
                    multiline
                    rows={10}
                    defaultValue={dataStr}
                    variant="outlined"
                    fullWidth
                    margin="normal"
                ></TextField>
                <Box mt={2} display="flex" justifyContent="flex-end">
                    <Button sx={{ marginRight:'10px'}} onClick={ () => {
                        handleCopy();
                    }}></Button>
                    <Button onClick={handleClose}></Button>
                </Box>
                <Snackbar
                    open={alertOpen}
                    autoHideDuration={3000}
                    onClose={handleAlertClose}
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                >
                    <Alert onClose={handleAlertClose} severity="success" sx={{ width: '100%' }}>
                         ~<br></br>  ~
                    </Alert>
                </Snackbar>

            </Box>

        </Modal>
    );
}

export default ExportModal;