import React, { useEffect, useState } from 'react';

import Grid from '@mui/material/Grid2';
import { FormControl, InputLabel, Select, MenuItem, 
    TextField, Button, Paper, Typography, 
    Stack,
    Chip,
    stepButtonClasses, Box,
    ButtonGroup,
    Alert, Snackbar } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import DeleteIcon from '@mui/icons-material/Delete';
import { alignProperty } from '@mui/material/styles/cssUtils';
import { grey } from '@mui/material/colors';
import useSerialPort from './useSerialPort';

function DeviceConnection() {
    const { connectPort, disconnectPort, port } = useSerialPort();
    const [isConnected, setIsConnected] = useState(false);
    const [connectBaudRate, setconnectBaudRate] = useState(9600);
    const [dataBits, setDataBits] = useState(8);
    const [stopBits, setStopBits] = useState(1);
    const [parity, setParity] = useState('none');
    const [alertOpen, setAlertOpen] = useState(false);

    //     mui : Snackbar Alert
    const handleAlertClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setAlertOpen(false);
    };

    //    
    useEffect(() => {
        const savedConnectSettings = localStorage.getItem('serialConnectSettings');
        if(savedConnectSettings){
            const { connectBaudRate, dataBits, stopBits, parity } = JSON.parse(savedConnectSettings);
            setconnectBaudRate(connectBaudRate);
            setDataBits(dataBits);
            setStopBits(stopBits);
            setParity(parity);
        }
    }, []);

    const handleConnectDevice = async () => {
        //  
        localStorage.setItem('serialConnectSettings', JSON.stringify(
            {connectBaudRate, dataBits, stopBits, parity}
        ));

        const options = { 
            baudRate: Number(connectBaudRate),
            dataBits: Number(dataBits),
            stopBits: Number(stopBits),
            parity: parity,
            flowControl: 'none' //   
        };

        try {
            await connectPort(options);
            setIsConnected(true);
            setAlertOpen(true);
        }
        catch (error) {
            setIsConnected(false);
            setAlertOpen(false);
        }
    };

    const handleDisconnectDevice = async () => {
        try {
            await disconnectPort();
            setIsConnected(false);
        } catch (error) {
            console.error('! Error disconnecting:', error);
        }
    };

    // const connectDevice = async () => {
    //     try {
    //         const port = await navigator.serial.requestPort();
    //         await port.open({ 
    //             baudRate: Number(connectBaudRate),
    //             dataBits: Number(dataBits),
    //             stopBits: Number(stopBits),
    //             parity: parity,
    //             flowControl: 'none' //   
    //         });
    //         setIsConnected(true);
    //         onConnect(port);

    //         //    
    //         port.addEventListener('disconnect', () => {
    //             setPort(null);
    //         });


    //         setAlertOpen(true);
    //     } catch (error) {
    //         console.error('! Error connecting:', error);
    //     }
    // };

    // const disconnectDevice = async () => {
    //     console.log("  ");
    //     if(!port) return;

    //     if(port.readable || port.writable) {
    //         let writer;
    //         let reader;
    //         try {
    //             writer = port.writable.getWriter();
    //             reader = port.readable.getReader();
    //             await writer.releaseLock();
    //             await reader.close();
    //             await port.close();
    //             setIsConnected(false);
    //             setPort(null);
    //         } catch (error) {
    //             console.error('! Error disconnecting:', error);
    //             alert('   .');
    //         } finally {
    //             if(writer) {
    //                 writer.releaseLock();
    //             }
    //             if(reader) {
    //                 reader.close();
    //             }
    //         }
    //     }
    // }

    //     Modal
    const handleShowPortInfo = async () => {
        console.log("   ");
        if(!port) {
            alert('    .');
            return
        } 
        
        try {
            const info = await port.getInfo();

            // readable, writable, locked  
            const readable = port.readable ? 'Yes' : 'No';
            const writable = port.writable ? 'Yes' : 'No';
            const locked = port.locked ? 'Yes' : 'No';
    
            //    
            const portStatus = {
                usbVendorId: info.usbVendorId ? `0x${info.usbVendorId.toString(16).toUpperCase()}` : 'Unknown',
                usbProductId: info.usbProductId ? `0x${info.usbProductId.toString(16).toUpperCase()}` : 'Unknown',
                manufacturer: info.manufacturer || 'Unknown',
                product: info.product || 'Unknown',
                serialNumber: info.serialNumber || 'Unknown',
                readable: readable,
                writable: writable,
                locked: locked,
            };
    
            alert(` \n\n${JSON.stringify(portStatus, null, 2)}`);
        } catch (error) {
            console.error('    . : ', error);
            alert('    .');
        }

    }

    return (
        <>
        <Snackbar
            open={alertOpen}
            autoHideDuration={3000}
            onClose={handleAlertClose}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        >
            <Alert onClose={handleAlertClose} severity="success" sx={{ width: '100%' }}>
                  ~<br></br>  ~
            </Alert>
        </Snackbar>
        <Box p={2} display='flex' justifyContent='center' alignItems='center'>
            <Grid container spacing={2} sx={{justifyContent:'center'}}
                rowSpacing={0}>
                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:100}}>
                        <InputLabel id="baudRate-label">BaudRate</InputLabel>
                        <Select
                            labelId="baudRate-label"
                            label="BaudRate"
                            id="baudRate"
                            size='small'
                            value={connectBaudRate}
                            onChange={(e) => setconnectBaudRate(Number(e.target.value))}
                        >
                            {[1200, 2400, 4800, 9600, 19200, 38400, 57600, 115200].map((baudRate) => (
                                <MenuItem key={baudRate} value={baudRate}>{
                                    baudRate}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>
            
                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:60}}>
                        <InputLabel id="dataBits-label">dataBits</InputLabel>
                        <Select
                            labelId="dataBits-label"
                            label="dataBits"
                            id="dataBits"
                            size='small'
                            value={dataBits}
                            onChange={(e) => setDataBits(Number(e.target.value))}
                        >
                            {[8, 7].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:60}}>
                        <InputLabel id="stopBits-label">stopBits</InputLabel>
                        <Select
                            labelId="stopBits-label"
                            label="stopBits"
                            id="stopBits"
                            size='small'
                            value={stopBits}
                            onChange={(e) => setStopBits(Number(e.target.value))}
                        >
                            {[1, 2].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <FormControl fullWidth sx={{ m :1, minWidth:90}}>
                        <InputLabel id="parity-label">parity</InputLabel>
                        <Select
                            labelId="parity-label"
                            label="parity"
                            id="parity"
                            size='small'
                            value={parity}
                            onChange={(e) => setParity(e.target.value)}
                        >
                            {["none", "even", "odd"].map((bit) => (
                                <MenuItem key={bit} value={bit}>
                                    {bit}
                                </MenuItem>    
                            ))}
                        </Select>
                    </FormControl>
                </Grid>

                <Grid item xs={12} sm={6} md={3}>
                    <ButtonGroup variant='contained' size='small' sx={{m:1.5}}>
                        <Button endIcon={<SendIcon />} onClick={handleConnectDevice} disabled={isConnected}> </Button>
                        <Button endIcon={<DeleteIcon />} onClick={handleDisconnectDevice} disabled={!isConnected}> </Button>
                        <Button
                            variant='text'
                            onClick={handleShowPortInfo}
                            color={isConnected ? 'success' : 'warning'}
                        > </Button>
                    </ButtonGroup>
                </Grid>
        </Grid>
            
        </Box>
        </>
    );
}

export default DeviceConnection;